import React, { Component } from 'react';
import {Link, Page, Layout, DisplayText} from '@shopify/polaris';

import conferences2017 from '../conferences/2017/ux';
import ConferenceList from '../ConferenceList';

class App extends Component {
  render() {
    return (
      <Page>
        <Layout>
          <Layout.Section>
            <DisplayText size="extraLarge">The conference list</DisplayText>
            <DisplayText size="small">An exaustive list</DisplayText>
          </Layout.Section>
          <Layout.Section>
            <ConferenceList conferences={conferences2017} />
          </Layout.Section>
          <Layout.Section>
            <Link
              url="https://github.com/nimzco/the-conference-list/issues/new"
              external
            >
              Suggest a conference
            </Link>
          </Layout.Section>
        </Layout>
      </Page>
    );
  }
}

export default App;



